import style from './ProfileInfo.module.css'
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatus from "./ProfileStatus";
import  {Upload_button} from "../../common/Upload_button/Upload";


let ProfileInfo = (props) => {
    if(!props.profile){
        return (
            <Preloader/>
        )
    }
    return(<div className={style.wallpaper}>
        <div className={style.description}>
            <img className={style.userphoto} src={props.profile.photos.small ? props.profile.photos.small : 'https://homemania.ru/img/noavatar.png'}/>
            <div>{props.isOwner && <Upload_button uploadPhoto={props.uploadPhoto}/>}</div>
            <div className={style.name}>{props.profile.fullName}</div>
            <ProfileStatus status={props.status} updateStatus = {props.updateStatus}/>
        </div>
    </div>)
}

export default ProfileInfo;